import Api from './api';

export default (name,lastName,email,password,pro)=>{
    return new Promise(async(resolve,reject)=>{
        let res = await Api.Request("auth/register",{
            name:name,
            lastName:lastName,
            email:email,
            password:password,
            admin:pro
        },{
            'Content-Type': 'application/json; charset=UTF-8'
        });
        console.log(res);
        if(!res.error){
            resolve(res);
        }else{
            reject(res.error);
        }
    })
}